.experienceWrapper {
  margin-top: 30vh;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* align-content: space-around; */
  color: white;
  padding: 50px;
  justify-content: center;
  align-items: center;
}

.experienceContainer {
  width: 100%;
  min-height: auto;
  padding: 50px;
  border-radius: 20px;
  box-shadow: 13px 13px 18px #040d1b, -13px -13px 15px #122746,
    inset 13px 13px 18px #040d1b, inset -13px -13px 19px #122746;
}

.experienceRow {
  width: 100%;
  min-height: auto;
  padding: 0 50px;
}

.experienceCol {
  margin-right: 10px;
  min-height: auto;
}

.card,
.naylorCard,
.mvcCard,
.srmCard,
.stemCard {
  background-color: #0a192f;
  border: none;
  width: 100%;
}

.title {
  color: #5ce9cb;
  -webkit-text-stroke: 1px black;
}

.subTitle {
  -webkit-text-stroke: 1px black;
}

.hr {
  background-color: #5ce9cb;
  color: #5ce9cb;
}

.cardText {
  padding: 5px 0;
  color: #8892b0;
}

.ul {
  padding: 50px;
}

.li {
  list-style: none;
  padding: 25px 30px;
  color: #8892b0;
  cursor: pointer;
  align-items: center;
}

.li:hover {
  background: #5de9cb0c;
  color: #5ce9cb;
  border-radius: 10px;
  transition: 0.4s;
}

.list {
  padding: 15px 0px;
  color: #a3abc2;
  list-style: none;
}

.list::before {
  content: "â–¹";
  position: absolute;
  left: 25px;
  color: #5ce9cb;
}

.link {
  text-decoration: none;
  color: #5ce9cb;
  font-weight: 700;
  line-height: 1em;
  font-size: 1.5em;
  padding: 20px;
  border-radius: 10px;
}

.link:hover {
  color: #0a192f;
  background: #00ffc8;
  transition: 0.4s;
}
